*{
    box-sizing: border-box;
}

html, body{
    height: 100%;
    background-color: aliceblue;
    
    
}

#root {
    
}

.container{
    background-color: aliceblue;
    height: 100%;
   
}

.loader{
    position: absolute;
    top:50%;
    width: 100%;
    font-style: oblique;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.movies {
    
    margin: auto;
    display: flex;
    position: relative;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 70px;
    width: 80%;
    align-items: flex-start;
    font-weight: 400;
    
    
    
}
.movies .movie{
    width: 100%;
    background-color: white;
    display: flex;
    box-shadow: 1.5px 1.5px 1.5px gray;
    
    

    

}

.movies img{
    width: 100%;
    position: relative;
    max-width: 170px;
    max-height: 260px;
    box-shadow: 10px 10px 10px black;
    margin-right: 30px;
    top : -40px;
}

